@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
* {
  box-sizing: border-box;
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
  width: 0;
}

.bold {
  font-weight: 700;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

.app {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.5);
  color: rgb(255, 255, 255);
}

.app::before {
  content: '';
  background: url(./assets/bg.jpg) no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.container {
  max-width: 700px;
  height: calc(100vh - 75px);
  margin: auto;
  padding: 0, 1rem;
  position: relative;
  top: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.top {
  width: 100%;
  margin: 1rem auto;
}

.description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(270deg);
}

.bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem, auto;
  padding: 1rem;
  border-radius: 20px;
  background-color: rgb(255, 255, 255, 0.1);
}

.search {
  text-align: center;
  padding: 1rem;
}

input {
  padding: .7rem 1.5rem;
  font-size: 1rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  outline: none;
}

::placeholder {
  color: #fff;
}

.submitButton {
  width: 32px;
  height: 32px;
  margin-left: 5px;
  margin-top: 5px;
  cursor: pointer;
  border: 0px;
  position: absolute;
  background: url(./assets/magnifying_glass.png) no-repeat center center/cover;
}

.error-message {
  position: absolute; 
  top: 11%;
  left: 50%;
  width: 220px;
  text-align: center;
  transform: translate(-50%, -50%);
  background: rgba(255, 0, 0, 0.7);
  font-size: 1rem;
  border-radius: 25px;
}

.loader-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.loader {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 2s linear infinite;
}

.loader-text {
  margin-top: 10px;
}
  
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
